<template>
  <div class="container">
    <div class="form">
      <el-form label-width="100px" :inline="true" :model="lifeTableApprove" ref="lifeTableForm">
        <el-form-item label="机构代码:" prop="orgCode">
          <el-select placeholder="请选择机构" v-model="lifeTableApprove.orgCode">
            <el-option
              v-for="org in orgList"
              :key="org.orgCode"
              :label="org.orgName"
              :value="org.orgCode">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="申报状态:" prop="status">
          <el-select placeholder="全部" v-model="lifeTableApprove.status">
            <el-option
              v-for="status in statusList"
              :key="status.statusCode"
              :label="status.statusName"
              :value="status.statusCode">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button size="small" @click="query" type="primary">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="data">
      <div class="table">
        <el-table style="width: 100%"  :data="currentData"  stripe :header-cell-style="tableHeader">
          <template v-for="column in columns">
            <el-table-column
              header-align="center"
              v-if="column.prop!=='status'"
              :key="column.prop"
              :prop="column.prop"
              :min-width="column.width"
              :label="column.label"
              :align="column.align">
            </el-table-column>
            <el-table-column
              header-align="center"
              v-else
              :key="column.prop"
              :min-width="column.width"
              :label="column.label"
              :align="column.align">
              <template slot-scope="scope">
                <el-tag  v-if="scope.row.status==='已审批'" type="success">{{scope.row.status}}</el-tag>
                <el-tag  v-else-if="scope.row.status==='待审批'" type="warning">{{scope.row.status}}</el-tag>
                <el-tag  v-else>{{scope.row.status}}</el-tag>
              </template>
            </el-table-column>
          </template>
          <el-table-column header-align="center"  align="center" label="操作" min-width="100">
            <template slot-scope="scope">
              <el-button v-if="scope.row.status==='待审批'" type="text" size="small" @click="approval(scope.row.version)">审批</el-button>
              <el-button v-else type="text" size="small" @click="detail(scope.row)">查看</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <communal-pagination
        @search="search"
        :currentPage="currentPage"
        :pageSize="pageSize"
        :total="data.length">
      </communal-pagination>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
const CommunalPagination = () => import('@/components/CommunalPagination')
export default {
  name: 'index',
  components: {CommunalPagination},
  data () {
    return {
      lifeTableApprove: {
        orgCode: '',
        status: ''
      },
      orgList: [
        {orgCode: '000000', orgName: '北京分公司'},
        {orgCode: '000001', orgName: '天津分公司'},
        {orgCode: '000002', orgName: '上海分公司'}
      ],
      statusList: [
        {statusCode: '1', statusName: '待申报'},
        {statusCode: '2', statusName: '待审批'},
        {statusCode: '3', statusName: '已审批'},
        {statusCode: '4', statusName: '待修改'},
        {statusCode: '5', statusName: '需要重新申报'}
      ],
      columns: [
        { prop: 'version', width: 250, label: '版本号', align: 'center' },
        { prop: 'org', width: 200, label: '编制机构', align: 'center' },
        { prop: 'channel', width: 250, label: '渠道', align: 'center' },
        { prop: 'declarer', width: 150, label: '申报人', align: 'center' },
        { prop: 'createTime', width: 200, label: '申报时间', align: 'center' },
        { prop: 'status', width: 100, label: '申报状态', align: 'center' },
        { prop: 'latestApproval', width: 120, label: '最新审批人', align: 'center' }
      ],
      data: [
        {
          version: '0200120180701001',
          org: '北京分公司',
          channel: '网销车险',
          declarer: '刘芳',
          createTime: '2018/07/01',
          status: '已审批',
          latestApproval: '张三'
        },
        {
          version: '0200220180501002',
          org: '天津分公司',
          channel: '4S店保险中心车险',
          declarer: '张强',
          createTime: '2018/05/01',
          status: '待审批',
          latestApproval: '张三'
        },
        {
          version: '0200320180301003',
          org: '上海分公司',
          channel: '维修店专业车险',
          declarer: '李菲',
          createTime: '2018/03/01',
          status: '待申报',
          latestApproval: '张三'
        },
        {
          version: '0200120170701004',
          org: '北京分公司',
          channel: '电话车险',
          declarer: '刘芳',
          createTime: '2017/07/01',
          status: '待修改',
          latestApproval: '张三'
        },
        {
          version: '0200220170501005',
          org: '天津分公司',
          channel: '银行邮政代理',
          declarer: '张强',
          createTime: '2017/05/01',
          status: '需要重新申报',
          latestApproval: '张三'
        },
        {
          version: '0200320170301006',
          org: '上海分公司',
          channel: '网销车险',
          declarer: '李菲',
          createTime: '2017/03/01',
          status: '已审批',
          latestApproval: '张三'
        },
        {
          version: '0200120160701007',
          org: '北京分公司',
          channel: '网销车险',
          declarer: '刘芳',
          createTime: '2016/07/01',
          status: '待审批',
          latestApproval: '张三'
        },
        {
          version: '0200220160501008',
          org: '天津分公司',
          channel: '保险公司车险',
          declarer: '张强',
          createTime: '2016/05/01',
          status: '待申报',
          latestApproval: '张三'
        },
        {
          version: '0200320160301009',
          org: '上海分公司',
          channel: '网销车险',
          declarer: '李菲',
          createTime: '2016/03/01',
          status: '已审批',
          latestApproval: '张三'
        }
      ],
      currentPage: 1,
      pageSize: 2
    }
  },
  computed: {
    currentData () {
      const pageData = []
      const start = (this.currentPage - 1) * this.pageSize
      let end = this.currentPage * this.pageSize
      end = end <= this.data.length ? this.currentPage * this.pageSize : this.data.length
      for (let i = start; i < end; ++i) {
        pageData.push(this.data[i])
      }
      return pageData
    },
    ...mapState({tableHeader: state => state.app.tableHeader})
  },
  methods: {
    search (pageNumber, pageSize) {
      this.currentPage = pageNumber
      this.pageSize = pageSize
      console.log(pageNumber)
    },
    approval (version) {
      this.$router.push({path: '/admin/individual/approval/detail'})
    },
    detail (version) {
      this.$router.push({path: '/admin/individual/query/detail'})
    },
    query () {
      this.currentPage = 1
    }
  }
}
</script>

<style scoped>

</style>



